@import "../../global.css";
.home{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 1.5rem;
    height: 100vh;
}

.home .image{
    flex: 1 1 40rem;

}
.home .image img{
    width: 90%;
}

.home .content {
    flex: 1 1 40rem;
}

.home .content h3{
    font-size: 4.5rem;
    color: #111;
    text-transform: uppercase;

}
.home .content h3 span{
    color: var(--main-color);
    text-transform: uppercase;

}
.home .content .info{
    font-size: 2.5rem;
    color: #111;
    padding: 0.5rem 0;
}
.home .content .text{
    font-size: 1.7rem;
    color: #666;
    padding: 0.5rem 0;
    text-align: justify;
}