@import "../../global.css";
.about .row-1{
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    padding-bottom: 2rem;
    height: auto;
}
.about .row-1 .image{
    flex: 1 1 5rem;
    height: 350px;
    width: 350px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.about .row-1 .image img{
    width: 60%;
    height: 80%;
    object-fit: cover;
  border-radius: 0.5rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;

}

.about .row-1 .content {
    flex: 1 1 50rem;
}

.about .row-1 .content h3{
    color: #333;
    font-size: 3.5rem;
}
.about .row-1 .content p{
    color: #666;
    font-size: 1.7rem;
    padding: 0.5rem 0;
}
.about .row-1 .content .box-container{
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    padding: 0.5rem 0;
}
.about .row-1 .content .box-container .box{
    flex: 1 1 20rem;
}
.about .row-1 .content .box-container .box span{
    color: var(--main-color);
}

  

.about .row-2 {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    align-items: center;
}
.about .row-2 .skills {
    flex: 1 1 40rem;
  }
.about .row-2 .skills .progress{
    padding: 1rem 0;

}
.about .row-2 .skills .progress h3{
    display: flex;
    justify-content: space-between;
    padding: 0.7rem 0;
    font-size: 1.7rem;
    color: #111;
    font-weight: 400;

}
.about .row-2 .skills .progress .bar{
    width: 100%;
    background: #ccc;
    overflow: hidden;
    height: 1rem;
    border-radius: 50rem;

}
.about .row-2 .skills .progress .bar span{
    display: block;
    height: 100%;
    background: var(--main-color);
    border-radius: 50rem;
}
.about .row-2 .skills .progress:nth-child(1) .bar span{
    width: 95%;

}
.about .row-2 .skills .progress:nth-child(2) .bar span{
    width: 80%;

}
.about .row-2 .skills .progress:nth-child(3) .bar span{
    width: 75%;

}
.about .row-2 .skills .progress:nth-child(4) .bar span{
    width: 85%;
}

.about .row-2 .box-container{
    flex: 1 1 40rem;
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
}
.about .row-2 .box-container .box{
    flex: 1 1 15rem;
    background: #FFF;
    padding: 2rem;
    border-radius: 0.5rem;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);


}
.about .row-2 .box-container .box h3{
    font-size: 3rem;
    color: var(--main-color);
}
.about .row-2 .box-container .box p{
    color: #666;
    font-size: 1.7rem;
    padding-top: 0.5rem;
}


@media (max-width:450px){
 
    .about .row-1 .image img{
        width: 100%;
        height: 100%;
       
    
    }
}
