@import "../../global.css";
.service .box-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}
.service .box-container .box{
    flex: 1 1 30rem;
    text-align: center;
    padding: 2rem;
    background: #fff;
    border-radius: 0.5rem;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);

}
.service .box-container .box i{
    height: 6rem;
    width: 6rem;
    line-height: 6rem;
    font-size: 2.5rem;
    color: #fff;
    background: var(--main-color);
    border-radius: 50%;

}
.service .box-container .box .icons-service img{
    width: 50px;
    height: 50px;
}
.service .box-container .box h3{
    color: #444;
    font-size: 2.2rem;
    padding-top: 1rem;
}
.service .box-container .box p{
    color: #666;
    font-size: 1.7rem;
    padding-top: 0.5rem;
    
}

